@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Domine&display=swap);


body, h1, h2, h3, h4, h5, h6, p, ul, ol, table, dl, dd {
	margin: 0;
	padding: 0;
}
h1 {
	font-size: 16px;
}
a {
	text-decoration: none;
	color: #000;
}
ul, ol {
	list-style: none;
}
img {
	max-width: 100%;
	vertical-align: top;
	border: none;
}
.clearfix::before, .clearfix::after {
	content: "";
	display: block;
}
.clearfix::after {
	clear: both;
}
body {
	font-family: "Open Sans", "Noto Sans KR", sans-serif;
	font-size: 1em;
}

/* portfolio */
#portfolio {
	padding: 6.25em 0 4.375em;
	background-color: #f7f7f7;
}
#portfolio .inner {
	margin: 0 auto;
	max-width: 940px;
	text-align: center;
}
#portfolio .title h3 {
	font-size: 1.875em;
	font-weight: 600;
}
#portfolio .content {
	padding-top: 40px;
}
#portfolio .content .banner {
	padding-top: 24px;
}
#portfolio .content .banner li {
	float: left;
	margin-left: 2%;
	margin-bottom: 50px;
	width: calc((100% - 4%)/3);
}
#portfolio .content .banner li:nth-child(3n+1) {
	margin-left: 0;
}
#portfolio .content .banner li a {
	display: block;
	position: relative;
	overflow: hidden;
}
#portfolio .content .banner li a::after {
	content: "";
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	z-index: 10;
	width: 100%;
	height: 100%;
	opacity: 0;
	background-color: rgba(252,207,255,.3);
	transition: opacity 0.3s;
}
#portfolio .content .banner li a:hover::after,
#portfolio .content .banner li a:focus::after {
	opacity: 1;
}
#portfolio .content .banner li a .photo {
	overflow: hidden;
}
#portfolio .content .banner li a .photo img {
	width: 100%;
	transform: scale(1);
	transition: transform 0.3s;
}
#portfolio .content .banner li a:hover .photo img,
#portfolio .content .banner li a:focus .photo img {
	transform: scale(1.05);
}
#portfolio .content .banner li a .desc {
	padding: 24px 10px;
	background-color: #fff;
}
#portfolio .content .banner li a .desc dl dt {
	line-height: 1.75;
	font-weight: 700;
}
#portfolio .content .banner li a .desc dl dd {
	line-height: 1.75;
	font-family: "Domine", serif;
	font-size: 0.875em;
	font-style: italic;
	color: #838383;
}

@media only screen and (max-width: 720px) {
	#portfolio .content .banner li {
		width: calc((100% - 2%)/2);
	}
	#portfolio .content .banner li:nth-child(3n+1) {
		margin-left: 2%;
	}
	#portfolio .content .banner li:nth-child(2n+1) {
		margin-left: 0;
	}
}
@media only screen and (max-width: 460px) {
	body {
		font-size: 0.875em;
	}
	#portfolio .content .banner li {
		width: 100%;
	}
	#portfolio .content .banner li:nth-child(n+1) {
		margin-left: 0;
	}
}